/* Main SCSS File */
html {
  font: normal normal normal 1rem/1.5 Helvetica Neue, Helvetica, Arial, sans-serif;
}

.vertical-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

button {
  display: block;
  margin: 20px;
  border: 1px solid #505050;
  font-size: 1.5rem;
  padding: 0.6666666667rem;
  border-radius: 3px;
  background: linear-gradient(to bottom, white, black);
  transform: scale(0.5);
}

body {
  background-image: url("../images/IMG_0384.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.interactive-object {
  position: absolute;
  cursor: pointer;
}

h1 {
  position: absolute;
  font-size: 50px;
  top: 40px; /*Adjust the distance from the top as needed */
  left: 230px; /*Adjust the distance from the left as needed */
  margin: 0;
  color: #ffffff; /* Replace this with your desired color in hexadecimal, RGB, or a named color */
}

.quote-container {
  position: absolute;
  font-size: 15px;
  top: 90px; /*Adjust the distance from the top as needed */
  left: 230px; /*Adjust the distance from the left as needed */
  max-width: 360px; /* Set a max-width for the quote container */
  max-height: 400px;
  margin: 0;
  color: #f17f5c; /* Replace this with your desired color in hexadecimal, RGB, or a named color */
}

#weather {
  position: absolute;
  font-size: 15px;
  top: 90%; /*Adjust the distance from the top as needed */
  left: 85%; /*Adjust the distance from the left as needed */
  margin: 0;
  color: #f17f5c;
}

#quote {
  margin-bottom: 10px; /* Adjust the space below the quote */
}

#author {
  margin-top: 10px; /* Adjust the space above the author */
}

.footer-container {
  position: absolute;
  font-size: 15px;
  top: 95%; /*Adjust the distance from the top as needed */
  left: 85%; /*Adjust the distance from the left as needed */
  margin: 0;
  color: #f17f5c;
}

#object1 {
  top: 28%;
  left: 5%;
  transform: scale(0.6); /* Scale the object to 50% of its original size */
}

#object2 {
  top: 40%;
  left: 50%;
  transform: scale(0.5);
}

#object3 {
  top: 0%;
  left: 80%;
  transform: scale(0.5);
}

#object4 { /*tree*/
  top: 50%;
  left: 10%;
  transform: scale(0.5);
}

#object5 { /*doll*/
  top: -30%;
  left: 60%;
  transform: scale(0.5);
}

#object6 { /*bubble*/
  top: 28%;
  left: 10%;
  transform: scale(0.5);
}

#object7 { /*bubble*/
  top: -5%;
  left: 48%;
  transform: scale(0.4);
}

#object8 { /*bubble*/
  top: 60%;
  left: 36%;
  transform: scale(0.5);
}

#object9 { /*bubble*/
  top: 40%;
  left: 68%;
  transform: scale(0.4);
}

.animated {
  animation: pulse 1s;
}

@keyframes pulse {
  0% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(0.5);
  }
}
@keyframes swing {
  0% {
    transform: scale(0.5) rotate(-5deg);
  }
  50% {
    transform: scale(0.5) rotate(5deg);
  }
  100% {
    transform: scale(0.5) rotate(-5deg);
  }
}
.swing-animation {
  animation: swing 3s alternate ease-in-out;
  /* transform-origin: top center;*/
}